#legend {
    position: absolute;
    bottom: 12px;
    left: 12px;
    height: auto;
    width: auto;
    padding: 20px 24px;
    background: white;
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    opacity: 0.7;
    z-index: 1;
    display: block;
    list-style-type: none;
  }
  #legend h4 {
    /* font: 800 12px/16px 'Montserrat'; */
    text-transform: uppercase;
    color: #2D3C43;
    margin-bottom: 12px;
  }
  #legend-content{
    /* font: 800 12px/16px 'Open Sans'; */
    margin-bottom: 6px;
  }
  .legend-content > li {
    margin-bottom: 6px;
    display: flex;
    align-items: center;
    text-transform: uppercase;
  }
  .legend-content > li:last-child {
    margin-bottom: 0;
  }
  .circle {
    width: 16px;
    height:16px;
    border-radius: 50%;
    margin-right: 16px;
  }