.layer-selector {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    background-color: #fff;
    width: 350px;
    margin: 24px;
    padding: 10 24px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    border-radius: 4px;
    padding: 10px;
  }
  .layer-selector h3 {
    font-size: 16px;
    margin: 8px 0;
  }
  .layer-selector .layout {
    display: table;
    width: 100%;
    margin-top: 8px;
  }
  .layer-selector .layout > * {
    display: table-cell;
    height: 12px;
  }
  .right-align {
    text-align: right;
  }


